*{
    margin: 0;
}


@media screen and (max-width:480px){
    .home .navbar .logo{
        display: none;
    }
    .home .container{
        width: 98%;
    }
}


@media screen and (max-width:768px){
    .home .navbar .logo{
        display: none;
    }
    .home .container{
        width: 95%;
    }
      .navbar .user button{
        position: absolute;
        bottom: 10px;
    }

}

@media screen and (max-width:1200px){
    
}

.formContainer{
    background:#682034;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.formWrapper{
    background-color: #a28089;
    padding: 40px 60px;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.formWrapper form{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.formWrapper form input{
    padding: 15px;
    border: none;
    border-bottom: 1px solid red;
    width: 250px;
    outline: none;
}
.formWrapper form label{
    display: flex;
    align-items: center;
    gap: 10px;
    color: cornsilk;
    font-size: 13px;
    cursor: pointer;
}
.formWrapper form label img{
    height: 32px;
    width: 32px;
}
.formWrapper form input::placeholder{
    color: rgb(175,175,175);
    letter-spacing: 1.5px;
}
.formWrapper form button{
    border-radius: 10px;
    padding: 10px;
    background-color: #682034;
    color: whitesmoke;
    letter-spacing: 1px;
    border: none;
    cursor: pointer;
}
.formWrapper form button:hover{
    
    background-color: whitesmoke;
    color: #682034;
    transition: 0.2s;
    
}
.formWrapper form p{
    color: cornsilk;
    font-size: 13px;
    text-align: center;
    letter-spacing: 1px;
}
.formWrapper .logo{
    color: white;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 1.5px;
}
.formWrapper .title{
    color: cornsilk;
    font-size: 18px;
    font-weight:300;
    text-align: center;
    letter-spacing: 1px;
}

.home{
  background-color: #a28089;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.home .container{
    border-radius: 10px;
    width: 90%;
    height: 95%;
    display: flex;
    overflow: hidden;
}
.sidebar{
    flex:1;
    position: relative;
    background-color: #70424f;
}
.chat{
    flex:2;
    background-color: #765c63;
}

.navbar {
    display: flex;
    align-items: center;
    background-color: #682034;
    height: 50px;
    padding: 10px;
    justify-content: space-between;
    color: whitesmoke;
}
.navbar .logo{
    font-size:24px;
    font-weight: bold;
    cursor: pointer;
    color: lightgray;
}
.navbar .user{
    display: flex;
    gap: 10px;  
}
.navbar .user img{
    background-color: aliceblue;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    object-fit: cover;
}
.navbar .user span{
    font-size: 20px;
    color:#b5a8ac;
}
.navbar .user button{
    border: none;
    height: 24px;
    cursor: pointer;
    border-radius: 10px;
    text-align: center;
    padding: 5px 10px;
    background-color: #a28089;
    color: whitesmoke;
    text-decoration: none;
}
.navbar .user button a{
    text-decoration: none;
    color: whitesmoke;
}
.navbar .user button:hover{
    background-color: #70424f;
    color: #a7a1a2;
    transition: 0.2s;
    border: 0.5px solid lightgray;
}

.sidebar .search{
    border-bottom: 1px solid grey;
}
.sidebar.search .searchForm{
    padding: 10px;
}
.sidebar .search input{
background-color: transparent;
border: none;
color: whitesmoke;
width: 100%;
outline: none;
height: 40px;
padding: 0px 10px;
}
.sidebar .search input::placeholder{
    color:lightgray;
    font-size: large;
}
.sidebar .userChat{
    padding: 10px;
    display: flex;
    align-items: center;
    gap: 10px;
    color: #efd8df;
    cursor: pointer;
}
.sidebar .userChatInfo  span{
    font-weight: bold;
    font-size: 20px;
}
.sidebar .userChatInfo p{
    color: rgb(215, 210, 210);
}
.sidebar .userChat:hover{
    background-color:#682034;
}
.sidebar .userChat img{
    width: 50px; 
    height: 50px;
    border-radius:50%;
    object-fit:cover;
}
.chat .chatInfo{
    height: 50px;
    background-color: #70424f;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: lightgray;
    font-weight: 700;
    padding: 10px;
    font-size: 20px;
    text-shadow:0px 2px 2px 2px rgb(144, 121, 121);
}
.chat .chatIcons img{
width: 24px;
height: 24px;
border-radius: 50%;
padding: 3px;
cursor: pointer;
box-shadow:0px 2px 2px 2px rgb(144, 121, 121);
}
.chat .chatIcons{
    display: flex;
    gap: 10px;
}
.chat .messages{
    background-color: #ba9da6;
    padding:10px;
    height: calc(100% - 160px);
    overflow: scroll;
}
 .input{
    height: 50px;
    background-color: whitesmoke;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.chat input{
    width: 100%;
    border: none;
    font-size: larger;
    background-color: whitesmoke;
    outline: none;
    color: #682034;
}
.chat input ::placeholder{
    color: lightgray;
}
.input .send{
    display: flex;
    gap:15px;
}
.input .send img{
    cursor: pointer;
}
.input .send button{
    cursor: pointer;
    background-color: #682034;
    color: lightgray;
    border: none;
    outline: none;
    border-radius: 12px;
    padding: 2px 10px;
}
.input .send button:hover{
    background-color: #e01350;
    color: white;
    transition: 0.2s;
}

.message .messageInfo img{
    width: 40px;
    border-radius: 50%;
}
.message .messageInfo{
    display: flex;
    flex-direction: column;
}
.message .messageInfo span{
    color:#682034;
}
.message{
    display: flex;
    gap: 20px;
}

 
.message .messageContent img{
    width: 220px;
    height: 250px;
    border-radius: 10px;
    margin: 10px;
    display: flex;
    flex-direction: column;
}